import React, {useState} from "react";
import {NavLink} from "react-router-dom";

const Navbar = () => {

    const [show, setShow] = useState(false);
  return (
    <>
      <section className="navbar-bg">
        <nav className="navbar navbar-expand-lg navbar-light">
          <div className="container">
            <a className="navbar-brand" href="#">
            <span className="logo" id="red">FIT-</span>MATE
            </a>
            <button
              className="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
              onClick={() => setShow(!show)}
            >
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className={`collapse navbar-collapse ${show ? "show" : ""}`}>
              <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                <li className="nav-item">
                  <NavLink className="nav-link active" aria-current="page" to="/">
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/workout">
                    Workout
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/food">
                    Food
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/tools">
                    Tools
                  </NavLink>
                </li>
                {/* <li className="nav-item">
                  <NavLink className="nav-link" to="/blogs">
                    Blogs
                  </NavLink>
                </li> */}
                <li className="nav-item">
                  <NavLink className="nav-link" to="learn">
                    Learn
                  </NavLink>
                </li>
               
              </ul>
              {/* <form className="d-flex">
                <button className="btn  btn-style" type="submit">Sign up</button>
                <button className="btn  btn-style btn-style-border" type="submit">Log in</button>
              </form> */}
            </div>
          </div>
        </nav>
      </section>
    </>
  );
};

export default Navbar;
